@import '../../../constants.scss';

.headerWrapper {
    margin: 10px auto 50px;
    background: #1C1C21;
    height: 50px;
    display: flex;
    align-items: center;
    width: 100%;
    box-shadow: 0px 24px 32px rgba(0, 0, 0, 0.04), 0px 16px 24px rgba(0, 0, 0, 0.04), 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 0px 1px rgba(0, 0, 0, 0.04);
}

.button {
    background: transparent;
    border: none;
    cursor: pointer;
    margin-left: 20px;
    font-size: 20px;
    font-weight: 600;
    color: $whitecolor;
}